// App
import React from "react";

// Router
import { Link } from "react-router-dom";

// Elemnts
import Input from "../../Elements/Input";
import questions from "../../config/questions";

class FAQ extends React.Component {
  state = {
    q: "",
  };

  render() {
    return (
      <div className={`faq-page`}>
        <div className="container-fluid">
          <h2 className="faq-title">Ð§Ð°ÑÑÐ¾ Ð·Ð°Ð´Ð°Ð²Ð°ÐµÐ¼ÑÐµ Ð²Ð¾Ð¿ÑÐ¾ÑÑ</h2>
          <div className="row">
            <div className="col">
              <Input
                type="text"
                placeholder="Ð§ÑÐ¾ Ð¸ÑÐµÐ¼?"
                value={this.state.q}
                onChange={(e) => {
                  this.setState({ q: e.target.value });
                }}
              />
            </div>
          </div>

          <div className="faq-questions row">
            {questions.map((question) => {
              let view = false;

              let q = this.state.q.split(" ");

              if (
                question.questions.find((x) => {
                  for (let i = 0; i < q.length; i++) {
                    if (
                      !(
                        ~x.title.toLowerCase().indexOf(q[i].toLowerCase()) ||
                        q[i] === 0
                      )
                    )
                      return false;
                  }
                  return true;
                })
              )
                view = true;
              else view = false;
              if (this.state.q.length === 0) {
                view = true;
              }

              return (
                view && (
                  <div
                    className="faq-question col-12 col-sm-6 col-md-4 col-lg-3"
                    key={question.id}
                  >
                    <h4>{question.title}</h4>
                    <ul>
                      {question.questions.map((question) => {
                        let view = false;
                        let q = this.state.q.split(" ");

                        for (let i = 0; i < q.length; i++) {
                          if (
                            ~question.title
                              .toLowerCase()
                              .indexOf(q[i].toLowerCase() || q[i])
                          ) {
                            view = true;
                          } else {
                            view = false;
                            break;
                          }
                        }

                        if (this.state.q.length === 0) {
                          view = true;
                        }

                        return (
                          view && (
                            <li key={question.id}>
                              <Link to="/">{question.title}</Link>
                            </li>
                          )
                        );
                      })}
                    </ul>
                  </div>
                )
              );
            })}
          </div>
        </div>
      </div>
    );
  }
}

export default FAQ;
